/* Footer Root */
.footer {
  background-color: var(--primary); /* ŸäŸÖŸÉŸÜ ÿ™ÿ∫ŸäŸäÿ±Ÿá ÿ≠ÿ≥ÿ® ÿßŸÑÿ£ŸÑŸàÿßŸÜ ÿßŸÑÿ±ÿ¶Ÿäÿ≥Ÿäÿ© */
  color: white;
  padding: 60px 20px 30px 20px;
  font-family: "Cairo", sans-serif;
}

/* ÿßŸÑÿ≠ÿßŸàŸäÿ© ÿØÿßÿÆŸÑ ÿßŸÑŸÅŸàÿ™ÿ± */
.footer .footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 40px;
}

/* ŸÉŸÑ ŸÇÿ≥ŸÖ ÿØÿßÿÆŸÑ ÿßŸÑŸÅŸàÿ™ÿ± */
.footer .footer-section {
  flex: 1;
  min-width: 200px;
}

/* ÿπŸÜÿßŸàŸäŸÜ ÿßŸÑÿ£ŸÇÿ≥ÿßŸÖ */
.footer .footer-section h4 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #fff;
  position: relative;
}

.footer .footer-section h4::after {
  content: "";
  width: 40px;
  height: 2px;
  background-color: #ffdd57;
  position: absolute;
  bottom: -6px;
  left: 0;
  border-radius: 2px;
}

/* ÿ±Ÿàÿßÿ®ÿ∑ ÿßŸÑŸÅŸàÿ™ÿ± */
.footer .footer-section ul {
  list-style: none;
  padding: 0;
}

.footer .footer-section ul li {
  margin-bottom: 10px;
}

.footer .footer-section ul li a {
  color: white;
  text-decoration: none;
  font-size: 14px;
  transition: color 0.3s ease;
}

.footer .footer-section ul li a:hover {
  color: #ffdd57;
}

/* ŸÖÿπŸÑŸàŸÖÿßÿ™ ÿßŸÑÿ™ŸàÿßÿµŸÑ */
.footer .contact-info p {
  margin: 6px 0;
  font-size: 14px;
}

.footer .contact-info i {
  margin-right: 8px;
  color: #ffdd57;
}

/* ÿ£ŸäŸÇŸàŸÜÿßÿ™ ÿßŸÑÿ™ŸàÿßÿµŸÑ ÿßŸÑÿßÿ¨ÿ™ŸÖÿßÿπŸä */
.footer .social-icons {
  display: flex;
  gap: 15px;
  margin-top: 12px;
}

.footer .social-icons a {
  color: white;
  font-size: 20px;
  transition: all 0.3s ease;
}

.footer .social-icons a:hover {
  color: #ffdd57;
  transform: translateY(-3px);
}

/* ÿÆÿ∑ ŸÅÿßÿµŸÑ */
.footer hr {
  border-color: rgba(255, 255, 255, 0.3);
  margin: 30px 0;
}

/* ÿ≠ŸÇŸàŸÇ ÿßŸÑŸÜÿ¥ÿ± */
.footer .copyright {
  text-align: center;
  font-size: 14px;
  opacity: 0.8;
}

/* Responsive */
@media (max-width: 768px) {
  .footer .footer-container {
    flex-direction: column;
    gap: 30px;
  }
  .footer .footer-section {
    text-align: center;
  }
  .footer .social-icons {
    justify-content: center;
  }
}
